*{
  font-family: -apple-system, BlinkMacSystemFont, system-ui, "Segoe UI", Roboto, Ubuntu;
}

body {
  height :3000px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.text-shadow {
  text-shadow: 0px 10px 12px #b3b3b3;
}

.title-color
{background: linear-gradient(90deg, #C93226 0%, #0015f7 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  
}

.title{
  font-weight: bolder;
  font-size: 3em;
}

.pjbox{
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.pjbox-item{
  background-color: #F5F5F5;
  text-align: center;
  text-transform: none;
  font-weight: bold;
  width: 17em;
  height: 12em;
  margin-top: 5em;
  margin-inline: 2em;
  border-radius: 18% / 25%;
  box-shadow: 0px 10px 25px #c0c0c0;
}

.pjpic {
  height: 8em;
}

.pjtext {
  font-size: 1.7em;
  padding-top: 3em;
}

.skillset {
  margin-inline: 1.8em;
  height: 8em;
}

@keyframes rotating {
  from{
      transform: rotate(0deg);
  }
  to{
      transform: rotate(360deg);
  }
}
.rotating{
  animation: rotating 15s linear infinite;
}

.skillset-text{
  padding-inline: 20%;
  padding-top: 2em;
  text-size-adjust: 0.8em;
  text-align: justify;
}

@keyframes rotatetest {
  0%{
    transform : rotate(-5deg);
  }
  50%{
    transform: rotate(5deg);
  }
  100%{
    transform : rotate(-5deg);
  }
}
.test:hover {
  animation: rotatetest 0.8s ease infinite;
}

.side-title {
  font-weight: bolder;
  font-size: 3em;
  padding-inline-start: 3em;
  margin-bottom: 0.5em;
}

.about-me-section {
  padding-inline: 10em;
}

.footer {
  background-color: #F44336;
  padding: 5em;
  color: #ffffff;
}

.footer-title {
  font-size: 3em;
  font-weight: bold;
}
